import React from 'react'
import { Typography } from '@mui/material/'
import { Wrapper, Title, Accordion, AccordionSummary, AccordionDetails } from 'components/CommonComponent'
import usePanel from 'hooks/usePanel'
import NeutralizeComp from 'views/Mint/components/NeutralizeComp'
import { useWeb3React } from 'web3-react-core'
import AddGuaranteeFromWallet from './AddGuaranteeFromWallet'
import AddGuaranteeFromAvatar from './AddGuaranteeFromAvatar'
import useHashPowerToken from '../../../hooks/useHashPowerToken'

export default function CustomizedAccordions(props) {
  const { approveStatus, onApprove } = props
  const { active, chainId, account } = useWeb3React()
  const { walletTokenBalance } = useHashPowerToken()
  const { expanded, handleChange } = usePanel()
  return (
    <Wrapper>
      <Title>Improve Prepaid Interest Coverage</Title>
      <Accordion id="panel3" expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Add asset from my wallet to Prepaid Interest</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel3' && (
            <AddGuaranteeFromWallet
              active={active}
              account={account}
              chainId={chainId}
              walletTokenBalance={walletTokenBalance}
              onApprove={onApprove}
              approveStatus={approveStatus}
            />
          )}
        </AccordionDetails>
      </Accordion>
      <Accordion id="panel4" expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Transfer asset from Collateral to Prepaid Interest</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel4' && (
            <AddGuaranteeFromAvatar
              active={active}
              account={account}
              chainId={chainId}
              walletTokenBalance={walletTokenBalance}
              onApprove={onApprove}
              approveStatus={approveStatus}
            />
          )}
        </AccordionDetails>
      </Accordion>
      <Accordion id="panel5" expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 3. Neutralize outstanding debt</Typography>
        </AccordionSummary>
        <AccordionDetails>{expanded === 'panel5' && <NeutralizeComp />}</AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
