import React from 'react'
import { Typography } from '@mui/material/'
import { Wrapper, Title, Accordion, AccordionSummary, AccordionDetails } from 'components/CommonComponent'
import { useWeb3React } from 'web3-react-core'
import usePanel from 'hooks/usePanel'
import useHashPowerToken from 'hooks/useHashPowerToken'
import AddGuaranteeFromWallet from './AddGuaranteeFromWallet'
import AddCurrencyToAvatar from './AddCurrencyToAvatar'
import NetralizeComp from '../../Mint/components/NeutralizeComp'

export default function ImproveCollateralRatio(props) {
  const { expanded, handleChange } = usePanel()
  const { approveStatus, onApprove } = props
  const { walletTokenBalance } = useHashPowerToken()
  const { active, chainId, account } = useWeb3React()
  return (
    <Wrapper>
      <Title>Improve Collateral Ratio</Title>
      <Accordion id="panel7" expanded={expanded === 'panel7'} onChange={handleChange('panel7')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Add asset from my wallet to Prepaid Interest</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel7' && (
            <AddGuaranteeFromWallet
              active={active}
              account={account}
              chainId={chainId}
              walletTokenBalance={walletTokenBalance}
              onApprove={onApprove}
              approveStatus={approveStatus}
            />
          )}
        </AccordionDetails>
      </Accordion>
      <Accordion id="panel8" expanded={expanded === 'panel8'} onChange={handleChange('panel8')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Add asset from my wallet to Collateral</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel8' && (
            <AddCurrencyToAvatar
              active={active}
              account={account}
              chainId={chainId}
              walletTokenBalance={walletTokenBalance}
              onApprove={onApprove}
              approveStatus={approveStatus}
            />
          )}
        </AccordionDetails>
      </Accordion>
      <Accordion id="panel9" expanded={expanded === 'panel9'} onChange={handleChange('panel9')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 3. Neutralize outstanding debt</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel9' && <NetralizeComp />}
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
