import React, { useCallback, useState } from 'react'
import Methods from 'components/Methods'
import { Approve } from 'components/CommonComponent'
import tokens, { poolData } from 'config/constants/tokens'
import useToast from 'hooks/useToast'
import Assets from 'assets'
import Wallet from 'components/Menu/icons/Wallet'
import DataWrapper from 'components/DataWrapper'
import { TokenUtils, toSmallUnits } from 'utils/transformHelper'
import { useComputedIndexAction, usePositionInfo, usePositionAction } from 'hooks/news/usePoistion'
import { TransactionSubmittedInfo, TransactionSuccessInfo } from 'components/TransactionInfo'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import Logo from 'components/Logo'

function AddGuaranteeFromWallet(props) {
  const { walletTokenBalance, approveStatus, onApprove } = props
  const [transferVal, setTransferVal] = useState<number>(0)
  const [isDisabled, setDisabled] = useState(false)
  const { toastSuccess, toastError, toastInfo } = useToast()
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const { currentPosition, collateralIndex } = usePositionInfo()
  const { onUpdatePrepaidInterest, newDisplayCollateralRatio, newDisplayPrepaidInterestCoverage } =
    useComputedIndexAction()
  const { handleUpdatePosition, onAddPrepaidInterest } = usePositionAction()

  const changeTransferVal = useCallback(
    (e, type) => {
      const numberVal = type === 0 ? Number(e.target.value) : walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]
      const _transferAmount = toSmallUnits(
        TokenUtils.formatTokenAmount(numberVal, currentPosition.mintTokenInfo.settlementCurrency.decimals),
        currentPosition.mintTokenInfo.settlementCurrency.decimals,
      )
      if (numberVal > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol] || numberVal <= 0 || _transferAmount.isZero()) {
        setDisabled(true)
      } else {
        setDisabled(false)
      }
      onUpdatePrepaidInterest(numberVal)
      setTransferVal(type === 0 ? e.target.value : numberVal)
    },
    [currentPosition.mintTokenInfo.settlementCurrency.decimals, currentPosition.mintTokenInfo.settlementCurrency.symbol, onUpdatePrepaidInterest, walletTokenBalance],
  )

  const onSubmit = useCallback(async () => {
    try {
      if (!transferVal || (transferVal <= 0 && transferVal > walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol])) {
        toastError(ToastTitle.failed, 'Please enter a valid amount')
        return
      }
      setDisabled(true)
      const txInfo = await onAddPrepaidInterest(transferVal)
      toastInfo(
        ToastTitle.submited,
        <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
      )
      const result = await txInfo.wait()
      if (result) {
        toastSuccess(
          ToastTitle.success,
          <TransactionSuccessInfo content="Settlement added successfully" txHash={txInfo.hash} />,
        )
        setDisabled(false)
        setTransferVal(0)
        onUpdatePrepaidInterest(0)
        await handleUpdatePosition()
      }
    } catch (e: any) {
      setDisabled(false)
    }
  }, [transferVal, walletTokenBalance, currentPosition.mintTokenInfo.settlementCurrency.symbol, onAddPrepaidInterest, toastInfo, toastError, toastSuccess, onUpdatePrepaidInterest, handleUpdatePosition])

  return (
    <>
      <Methods
        SelectIcon={Wallet}
        options={[
          {
            token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
            balance: walletTokenBalance ? walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol] : 0,
            symbol: currentPosition.mintTokenInfo.settlementCurrency.symbol,
            icon: ()=> <Logo imgprops={{
              style: {marginRight: 10}
            }} token={currentPosition.mintTokenInfo.settlementCurrency.logo} symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol} />,
            address:  currentPosition.mintTokenInfo.settlementCurrency.address,
            rightOptionList: [
              {
                token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
                symbol: currentPosition.mintTokenInfo.settlementCurrency.symbol,
                icon: () => <Logo imgprops={{
                  style: {marginRight: 10}
                }} token={currentPosition.mintTokenInfo.settlementCurrency.logo} symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol} />,
                address:  currentPosition.mintTokenInfo.settlementCurrency.address,
                balance: collateralIndex.prepaidInterest,
              },
            ],
          },
        ]}
        rightBalance={collateralIndex.prepaidInterest}
        leftBalance={walletTokenBalance ? walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol] : 0}
        rightSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        selectedSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        RightIcon={() => <img src={Assets.PrepaidInterest} style={{ width: 20 }} alt="" />}
        leftInputValue={transferVal}
        rightInputValue={transferVal}
        selectInput={(e) => {
          changeTransferVal(e, 0)
        }}
        onMax={() => {
          changeTransferVal({}, 1)
        }}
      />
      <DataWrapper
        data={[
          [DataIndexName.CollateralRatio, collateralIndex.displayRatio, newDisplayCollateralRatio],
          [DataIndexName.PrepaidInterestCoverage, collateralIndex.displayCoverage, newDisplayPrepaidInterestCoverage],
        ]}
      />
      {approveStatus ? (
        <Approve style={{ boxShadow: 'none' }} disabled={isDisabled || transferVal <= 0} onClick={onSubmit}>
          Submit
        </Approve>
      ) : (
        <Approve
          style={{ boxShadow: 'none' }}
          disabled={approveBtnStatus}
          onClick={() => {
            onApprove((state) => {
              setApproveBtnStatus(state)
            })
          }}
        >
          Approve
        </Approve>
      )}
    </>
  )
}

export default AddGuaranteeFromWallet
