import React, { useCallback, useMemo, useState } from 'react'
import tokens from 'config/constants/tokens'
import Methods from 'components/Methods'
import Wallet from 'components/Menu/icons/Wallet'
import { AvatarIcon } from 'components/uikit'
import { Approve, IconWrap, TipWrap } from 'components/CommonComponent'
import { useTransfer } from 'hooks/useManager'
import DataWrapper from 'components/DataWrapper'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import { useComputedIndexAction, usePositionInfo, usePositionAction } from 'hooks/news/usePoistion'
import Logo from 'components/Logo'
import { chainConfigs, commonTokens, SupportedChainId, tokenKey } from 'config/constants/tokenConfig'
import useToast from 'hooks/useToast'
import { TransactionSuccessInfo } from 'components/TransactionInfo'

function AddCurrencyToAvatar(props) {
  const { onApprove, approveStatus, account, chainId, walletTokenBalance } = props

  const [transferVal, setTransferVal] = useState(0)
  const [submitStatus, setSubmitStatus] = useState(true)
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const { toastError, toastSuccess } = useToast()
  const { currentPosition, currentPositionTokenInfo, collateralIndex } = usePositionInfo()
  const { onUpdateCollateralValue, newDisplayCollateralRatio, newDisplayPrepaidInterestCoverage } =
    useComputedIndexAction()
  const { handleUpdatePosition } = usePositionAction()
  const transfer = useTransfer()

  const tradingList = useMemo(() => {
    const { allowedTokens } = currentPositionTokenInfo
    const tokenList = Array.from(Object.values(allowedTokens.tokens)).map((item) => {
      return {
        ...item,
        token: item.symbol,
        icon: () => <Logo imgprops={{ style: { marginRight: 10 } }} token={item.logo} symbol={item.symbol} />,
        balance: walletTokenBalance[item.symbol],
        rightOptionList: [
          {
            ...item,
            token: item.symbol,
            icon: () => <Logo imgprops={{ style: { marginRight: 10 } }} token={item.logo} symbol={item.symbol} />,
            balance: toLargeUnits(allowedTokens.balances[item.symbol], item.decimals),
          },
        ],
      }
    })
    return tokenList
  }, [currentPositionTokenInfo, walletTokenBalance])
  const [selectIndex, setSelectIndex] = useState<number>(0)

  const leftSelectedToken = useMemo(() => {
    return (
      (tradingList.length && tradingList[selectIndex]) || {
        ...currentPosition.mintTokenInfo.settlementCurrency,
        balance: walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol],
      }
    )
  }, [currentPosition.mintTokenInfo.settlementCurrency, selectIndex, tradingList, walletTokenBalance])
  const rightSelectedToken = useMemo(() => {
    return (
      (tradingList.length && tradingList[selectIndex].rightOptionList[0]) || {
        ...currentPosition.mintTokenInfo.settlementCurrency,
        balance: toLargeUnits(
          currentPositionTokenInfo.allowedTokens.balances[currentPosition.mintTokenInfo.settlementCurrency.symbol],
          currentPosition.mintTokenInfo.settlementCurrency.decimals,
        ),
      }
    )
  }, [
    currentPosition?.mintTokenInfo.settlementCurrency,
    currentPositionTokenInfo?.allowedTokens.balances,
    selectIndex,
    tradingList,
  ])
  const onTransfer = useCallback(async () => {
    try {
      setSubmitStatus(true)
      console.log('first', toSmallUnits(
        TokenUtils.formatTokenAmount(transferVal, commonTokens[leftSelectedToken.symbol].decimals),
        commonTokens[leftSelectedToken.symbol].decimals,
      ))
      const tx = await transfer(
        account,
        chainConfigs[chainId as SupportedChainId].tokens[leftSelectedToken.symbol].address,
        currentPosition?.positionAddress,
        toSmallUnits(
          TokenUtils.formatTokenAmount(transferVal, commonTokens[leftSelectedToken.symbol].decimals),
          commonTokens[leftSelectedToken.symbol].decimals,
        ),
      )
      if (tx) {
        setSubmitStatus(false)
        setTransferVal(0)
        toastSuccess(ToastTitle.success, <TransactionSuccessInfo content='Collateral added successfully.' txHash={tx.transactionHash} />)
        onUpdateCollateralValue({
          symbol: leftSelectedToken.symbol as tokenKey,
          value: 0,
        })
        await handleUpdatePosition()
      }
    } catch (e: any) {
      toastError(ToastTitle.failed, e?.reason)
      setSubmitStatus(false)
    }
  }, [
    transfer,
    account,
    leftSelectedToken,
    chainId,
    currentPosition,
    transferVal,
    toastSuccess,
    onUpdateCollateralValue,
    handleUpdatePosition,
    toastError,
  ])
  return (
    <>
      <Methods
        options={tradingList}
        rightOptions={tradingList}
        leftInputValue={transferVal}
        leftBalance={leftSelectedToken.balance}
        leftSelectValue={leftSelectedToken.symbol}
        rightInputValue={transferVal}
        // rightBalance={rightSelectedToken.balance}
        // rightSelectValue={rightSelectedToken.symbol}
        SelectIcon={Wallet}
        selectInput={({ target }) => {
          setTransferVal(target.value)
          console.log('target.value :>> ', target.value, leftSelectedToken.balance);
          if (target.value > 0 && target.value <= leftSelectedToken.balance) {
            const _transferVal = toSmallUnits(
              TokenUtils.formatTokenAmount(target.value, leftSelectedToken.decimals),
              leftSelectedToken.decimals,
            )
            console.log('_transferVal :>> ', _transferVal);
            setSubmitStatus(_transferVal.isZero())
          } else {
            setSubmitStatus(true)
          }

          onUpdateCollateralValue({
            symbol: leftSelectedToken.symbol as tokenKey,
            value: target.value,
          })
        }}
        leftChange={(selected) => {
          const _index = tradingList.findIndex((item) => item.symbol === selected.symbol)
          setSelectIndex(_index)
          setTransferVal(0)
          onUpdateCollateralValue({
            symbol: selected.symbol,
            value: 0,
          })
        }}
        onMax={() => {
          if (walletTokenBalance) {
            if (leftSelectedToken.balance) {
              setTransferVal(leftSelectedToken.balance)
              onUpdateCollateralValue({
                symbol: leftSelectedToken.symbol as tokenKey,
                value: leftSelectedToken.balance,
              })
              if (leftSelectedToken.balance > 0) {
                setSubmitStatus(false)
              }
            }
          }
        }}
        RightIcon={AvatarIcon}
      />
      <DataWrapper
        data={[
          [DataIndexName.CollateralRatio, collateralIndex.displayRatio, newDisplayCollateralRatio],
          [DataIndexName.PrepaidInterestCoverage, collateralIndex.displayCoverage, newDisplayPrepaidInterestCoverage],
        ]}
      />
      <TipWrap>
        <IconWrap style={{ background: '#FF7D00', color: '#fff' }}>!</IconWrap>
        <p>
          Add asset to Collateral wont effect the Prepaid Interest Coverage, so you might need to add the specific
          asset in method 1 first.
        </p>
      </TipWrap>
      {approveStatus ? (
        <Approve style={{ boxShadow: 'none' }} disabled={submitStatus || transferVal <= 0} onClick={onTransfer}>
          Submit
        </Approve>
      ) : (
        <Approve
          style={{ boxShadow: 'none' }}
          disabled={approveBtnStatus}
          onClick={() => {
            onApprove((state) => {
              setApproveBtnStatus(state)
            })
          }}
        >
          Approve
        </Approve>
      )}
    </>
  )
}

export default AddCurrencyToAvatar
