import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Typography } from '@mui/material/'
import Wallet from 'components/Menu/icons/Wallet'
import Methods from 'components/Methods'
import { useWeb3React } from 'web3-react-core'
import { AvatarIcon, GuaranteeIcon } from 'components/uikit'
import DataWrapper from 'components/DataWrapper'
import { Wrapper, Title, Accordion, AccordionSummary, AccordionDetails, Approve } from 'components/CommonComponent'
import usePanel from 'hooks/usePanel'
// import tokens from 'config/constants/tokens'
import useToast from 'hooks/useToast'
import contracts from 'config/constants/contracts'
import { ethers, utils } from 'ethers'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import useSystemParams from 'hooks/useSystemParams'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { useComputedIndexAction, usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import PositionABI from 'config/abi/Position.json'
import Logo from 'components/Logo'
import { chainConfigs, SupportedChainId, SupportedTokens, TokenConfig } from 'config/constants/tokenConfig'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import { getSwapQuote } from 'config/constants/pool'

const { ActionUser } = contracts
const ActionUserInterface = new utils.Interface(ActionUser.abi)
const WidrawFromPrepaidInterestToWallet = (props: { approveStatus: boolean; onApprove: any }) => {
  const { approveStatus, onApprove } = props
  const [actionDisable, setActionDisable] = useState(true)
  const { chainId, account } = useWeb3React()
  const { currentPosition, collateralIndex } = usePositionInfo()
  const [trading, setTrading] = useState(false)
  const [transferAmount, setTransferAmount] = useState<number>(0)
  const { toastError, toastSuccess, toastInfo } = useToast()
  const { walletTokenBalance } = useHashPowerToken()
  const { signer } = useSystemParams()
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)
  const {
    onUpdatePrepaidInterest,
    newDisplayCollateralRatio,
    newPrepaidInterestCoverage,
    newCollateralRatio,
    newDisplayPrepaidInterestCoverage,
    minCollateralValue,
  } = useComputedIndexAction()

  const { handleUpdatePosition } = usePositionAction()
  const onGuaranteeCurrencyToWallet = useCallback(async () => {
    try {
      setTrading(true)
      const data = ActionUserInterface.encodeFunctionData('moveGuarantee', [
        account,
        toSmallUnits(transferAmount.toString(), currentPosition.mintTokenInfo.settlementCurrency.decimals),
      ])
      const positionContract = new ethers.Contract(currentPosition.positionAddress, PositionABI, signer)
      const txInfo = await positionContract.delegateCall(ActionUser.address[chainId], data)
      if (txInfo) {
        setTransferAmount(0)
        toastInfo(
          ToastTitle.submited,
          <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
        )
        const result = await txInfo.wait()
        if (result) {
          setTrading(false)
          toastSuccess(
            ToastTitle.success,
            <TransactionSubmittedInfo content="The transactions was successful" txHash={txInfo.hash} />,
          )
          await handleUpdatePosition()
        }
      }
    } catch (e: any) {
      setTrading(false)
      toastError(ToastTitle.failed, e?.reason)
    }
  }, [
    account,
    chainId,
    currentPosition.mintTokenInfo.settlementCurrency.decimals,
    currentPosition.positionAddress,
    handleUpdatePosition,
    signer,
    toastError,
    toastInfo,
    toastSuccess,
    transferAmount,
  ])

  const onInput = (e) => {
    setTransferAmount(e.target.value)
  }
  useEffect(() => {
    if (
      newCollateralRatio < 120 ||
      newPrepaidInterestCoverage < 20 ||
      transferAmount <= 0 ||
      transferAmount > (collateralIndex.prepaidInterest as number) ||
      trading
    ) {
      setActionDisable(true)
    } else {
      setActionDisable(false)
    }
  }, [collateralIndex.prepaidInterest, newCollateralRatio, newPrepaidInterestCoverage, trading, transferAmount])

  useEffect(() => {
    if (transferAmount >= 0) {
      const val = transferAmount >= 0 ? Number(`-${transferAmount}`) : 0
      onUpdatePrepaidInterest(Number.isNaN(val) ? 0 : val)
    } else {
      onUpdatePrepaidInterest(0)
    }
  }, [onUpdatePrepaidInterest, transferAmount])

  const tradingList = useMemo(() => {
    return [
      {
        decimals: currentPosition.mintTokenInfo.settlementCurrency.decimals,
        token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
        balance: collateralIndex.prepaidInterest,
        icon: () => (
          <Logo
            imgprops={{ style: { marginRight: 10 } }}
            token={currentPosition.mintTokenInfo.settlementCurrency.logo}
            symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
          />
        ),
        rightOptionList: [
          {
            decimals: currentPosition.mintTokenInfo.settlementCurrency.decimals,
            token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
            balance: walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol],
            icon: () => (
              <Logo
                imgprops={{ style: { marginRight: 10 } }}
                token={currentPosition.mintTokenInfo.settlementCurrency.logo}
                symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
              />
            ),
          },
        ],
      },
    ]
  }, [collateralIndex, currentPosition, walletTokenBalance])

  return (
    <>
      <Methods
        selectInput={onInput}
        onMax={() => {
          const pimin = 20 * currentPosition.positionStatus.estimatedDailyDebt.toNumber()
          const pi = Number(
            TokenUtils.formatTokenAmount(
              Number(collateralIndex.prepaidInterest) -
                (toLargeUnits(pimin, currentPosition.mintTokenInfo.settlementCurrency.decimals, true) as number),
              currentPosition.mintTokenInfo.settlementCurrency.decimals,
            ),
          )
          if (pi > 0 && minCollateralValue >= 0) {
            if (pi > minCollateralValue) {
              setTransferAmount(Number(TokenUtils.formatFourDecimals(minCollateralValue)))
            } else {
              setTransferAmount(Number(TokenUtils.formatFourDecimals(pi)))
            }
          } else {
            setTransferAmount(0)
          }
        }}
        SelectIcon={GuaranteeIcon}
        options={tradingList}
        leftBalance={collateralIndex.prepaidInterest}
        rightBalance={walletTokenBalance && walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]}
        rightSelectedValue={42}
        rightSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        selectValue={42}
        selectedBalance={100}
        selectedSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        leftInputValue={transferAmount}
        rightInputValue={transferAmount}
        RightIcon={Wallet}
      />
      <DataWrapper
        data={[
          ['Collateral Ratio', collateralIndex.displayRatio, newDisplayCollateralRatio],
          ['Prepaid Interest Coverage', collateralIndex.displayCoverage, newDisplayPrepaidInterestCoverage],
        ]}
      />
      {approveStatus ? (
        <Approve style={{ boxShadow: 'none' }} disabled={actionDisable} onClick={onGuaranteeCurrencyToWallet}>
          Submit
        </Approve>
      ) : (
        <Approve
          style={{ boxShadow: 'none' }}
          disabled={approveBtnStatus}
          onClick={() => {
            onApprove((state) => {
              setApproveBtnStatus(state)
            })
          }}
        >
          Approve
        </Approve>
      )}
    </>
  )
}

const WidrawFromCollateralToWallet: React.FC<{
  approveStatus: boolean
  onApprove: (callback: (state: boolean) => void) => void
}> = (props) => {
  const { chainId, account } = useWeb3React()
  const { approveStatus, onApprove } = props
  const { signer } = useSystemParams()
  const { walletTokenBalance } = useHashPowerToken()
  const [transferAmount, setTransferAmount] = useState<number>(0)
  const { tokenPools } = useSystemParams()

  const [leftSelectedToken, setLeftSelectedToken] = useState<
    { balance: number; token: SupportedTokens } & Omit<TokenConfig, 'logo'>
  >({
    balance: 0,
    token: SupportedTokens.WBTC,
    decimals: 8,
    address: '',
    symbol: SupportedTokens.WBTC,
  })
  const [rightSelectedToken, setRightSelectedToken] = useState<
    { balance: number; token: SupportedTokens } & Omit<TokenConfig, 'logo'>
  >({
    balance: 0,
    token: SupportedTokens.WBTC,
    decimals: 8,
    address: '',
    symbol: SupportedTokens.WBTC,
  })
  const [actionDisable, setActionDisable] = useState(true)

  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const { currentPosition, currentPositionTokenInfo, collateralIndex } = usePositionInfo()
  const {
    onUpdateCollateralValue,
    newDisplayCollateralRatio,
    newCollateralRatio,
    newDisplayPrepaidInterestCoverage,
    minCollateralValue,
  } = useComputedIndexAction()
  const { handleUpdatePosition } = usePositionAction()
  const { toastError, toastSuccess, toastInfo } = useToast()
  const [trading, setTrading] = useState(false)

  useEffect(() => {
    if (leftSelectedToken) {
      const val = toSmallUnits(
        TokenUtils.formatTokenAmount(transferAmount, leftSelectedToken.decimals),
        leftSelectedToken.decimals,
      )
      if (!val.isZero() && transferAmount <= leftSelectedToken.balance && !trading) {
        setActionDisable(false)
      } else {
        setActionDisable(true)
      }
      if (newCollateralRatio <= 120) {
        setActionDisable(true)
      }
    }
  }, [leftSelectedToken, transferAmount, trading, newCollateralRatio])

  useEffect(() => {
    const transferAmountVal = Number(`-${transferAmount}`)
    onUpdateCollateralValue({
      symbol: leftSelectedToken.token as any,
      value: Number.isNaN(transferAmountVal) ? 0 : transferAmountVal,
    })
  }, [leftSelectedToken, onUpdateCollateralValue, transferAmount])

  const onTransfer = useCallback(async () => {
    setTrading(true)
    setActionDisable(true)
    if (transferAmount <= 0) {
      setTrading(false)
      toastError(ToastTitle.failed, 'Please enter a valid amount')
      return
    }
    const withdrawAssetParams = ActionUserInterface.encodeFunctionData('withdrawAsset', [
      chainConfigs[chainId as SupportedChainId].tokens[leftSelectedToken.token].address,
      account,
      toSmallUnits(
        TokenUtils.formatTokenAmount(transferAmount, leftSelectedToken.decimals),
        leftSelectedToken.decimals,
      ),
    ])
    try {
      const positionContract = new ethers.Contract(currentPosition.positionAddress, PositionABI, signer)
      const txInfo = await positionContract.delegateCall(ActionUser.address[chainId], withdrawAssetParams)
      if (txInfo.hash) {
        toastInfo(
          ToastTitle.submited,
          <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
        )
        const result = await txInfo.wait()
        if (result) {
          setTrading(false)
          toastSuccess(
            ToastTitle.success,
            <TransactionSubmittedInfo content="The transactions was successful" txHash={txInfo.hash} />,
          )
          setTransferAmount(0)
          await handleUpdatePosition()
        }
      }
    } catch (e: any) {
      setTrading(false)
      toastError(ToastTitle.failed, e?.reason)
    }
  }, [
    leftSelectedToken,
    chainId,
    account,
    transferAmount,
    currentPosition.positionAddress,
    signer,
    toastInfo,
    toastSuccess,
    handleUpdatePosition,
    toastError,
  ])

  const onMax = (type) => {
    const { settlementCurrency } = currentPosition.mintTokenInfo
    if (type === 1) {
      if (leftSelectedToken.token === settlementCurrency.symbol) {
        if (minCollateralValue < leftSelectedToken.balance) {
          setTransferAmount(Number(TokenUtils.formatFourDecimals(minCollateralValue)))
        } else {
          setTransferAmount(Number(TokenUtils.formatFourDecimals(leftSelectedToken.balance)))
        }
      } else {
        const unitPrice = getSwapQuote(
          chainId,
          leftSelectedToken.token,
          settlementCurrency.symbol,
          toSmallUnits(
            TokenUtils.formatTokenAmount(minCollateralValue, settlementCurrency.decimals),
            settlementCurrency.decimals,
          ),
          tokenPools,
        )
        const minCollateralValueTokenPrice = toLargeUnits(unitPrice, leftSelectedToken.decimals, true) as number
        if (minCollateralValueTokenPrice < leftSelectedToken.balance) {
          setTransferAmount(Number(TokenUtils.formatFourDecimals(minCollateralValueTokenPrice)))
        } else {
          setTransferAmount(Number(TokenUtils.formatFourDecimals(leftSelectedToken.balance)))
        }
      }
    }
  }

  const tradingList = useMemo(() => {
    const { allowedTokens } = currentPositionTokenInfo
    if (allowedTokens.tokens) {
      return Object.keys(allowedTokens.tokens).map((item) => {
        const token = allowedTokens.tokens[item as SupportedTokens]
        const current = {
          icon: () => <Logo imgprops={{ style: { marginRight: 10 } }} token={token.logo} symbol={token.symbol} />,
          token: token.symbol,
          balance: toLargeUnits(allowedTokens.balances[item as SupportedTokens], token.decimals, true) as number,
          decimals: token.decimals,
          address: token.address,
        }
        return {
          ...current,
          rightOptionList: [
            {
              ...current,
              balance: walletTokenBalance[item as SupportedTokens] as number,
            },
          ],
        }
      })
    }
    return []
  }, [currentPositionTokenInfo, walletTokenBalance])

  useEffect(() => {
    const activeToken =
      (tradingList.find((it) => it.token === currentPosition.mintTokenInfo.settlementCurrency.symbol) as any) ||
      (tradingList[0] as any)
    setLeftSelectedToken(activeToken)
    setRightSelectedToken(activeToken)
  }, [currentPosition.mintTokenInfo.settlementCurrency.symbol, tradingList])

  return (
    <>
      <Methods
        rightOptions={[rightSelectedToken]}
        SelectIcon={AvatarIcon}
        options={tradingList}
        leftSelectValue={leftSelectedToken ? leftSelectedToken.token : ''}
        rightSelectValue={rightSelectedToken ? rightSelectedToken.token : ''}
        rightBalance={rightSelectedToken.balance}
        leftInputValue={transferAmount}
        rightInputValue={transferAmount}
        leftChange={(selected) => {
          setLeftSelectedToken(selected)
          setRightSelectedToken(selected.rightOptionList[0])
          setTransferAmount(0)
        }}
        selectInput={(e) => {
          setTransferAmount(e.target.value)
          if (e.target.value > 0 && e.target.value <= leftSelectedToken.balance) {
            setActionDisable(false)
          } else {
            setActionDisable(true)
          }
        }}
        onMax={() => {
          onMax(1)
        }}
        RightIcon={Wallet}
      />
      <DataWrapper
        data={[
          [DataIndexName.CollateralRatio, collateralIndex.displayRatio, newDisplayCollateralRatio],
          [DataIndexName.PrepaidInterestCoverage, collateralIndex.displayCoverage, newDisplayPrepaidInterestCoverage],
        ]}
      />
      {approveStatus ? (
        <Approve style={{ boxShadow: 'none' }} disabled={actionDisable} onClick={onTransfer}>
          Submit
        </Approve>
      ) : (
        <Approve
          style={{ boxShadow: 'none' }}
          disabled={approveBtnStatus}
          onClick={() => {
            onApprove((state) => {
              setApproveBtnStatus(state)
            })
          }}
        >
          Approve
        </Approve>
      )}
    </>
  )
}

export default function WithdrawCollateral(props) {
  const { expanded, handleChange } = usePanel()
  return (
    <Wrapper>
      <Title>Withdraw Collateral</Title>
      <Accordion id="panel13" expanded={expanded === 'panel13'} onChange={handleChange('panel13')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Withdraw asset from Collateral to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>{expanded === 'panel13' && <WidrawFromCollateralToWallet {...props} />}</AccordionDetails>
      </Accordion>
      <Accordion id="panel14" expanded={expanded === 'panel14'} onChange={handleChange('panel14')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Withdraw asset from Prepaid Interest to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {expanded === 'panel14' && <WidrawFromPrepaidInterestToWallet {...props} />}
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
