import React from "react";
import {useAppDispatch, useAppSelector} from "../state";
import {toggleStatus} from "../state/CollateralManage/actions";

export default function usePanel(){
  const expanded = useAppSelector((state)=> state.collateralManage.expanded)
  const dispatch = useAppDispatch()
  const handleChange = (panel: string):(event: React.SyntheticEvent, newExpanded: boolean)=>void => {
    return (event: React.SyntheticEvent, newExpanded: boolean) => {
      dispatch(toggleStatus({expanded:newExpanded ? panel : '-1'}))
    }
  }
  const setExpanded = (panel?: string) => {
    dispatch(toggleStatus({expanded:panel || '-1'}))
  }
  return {expanded,handleChange,setExpanded}
}
