const getValueColor = (value: number) =>
  value * 1 >= 400 ? 'rgb(29, 202, 218)' : value * 1 > 200 ? 'rgb(255, 204, 4)' : 'rgb(255, 99, 8)'

function Manage() {
  const oBox: MutableRefObject<any> = React.useRef()
  const { expanded } = usePanel()
  const [approveStatus, setApproveStatus] = useState(false)

  const { currentPosition, currentPositionTokenInfo, currentMintToken } = usePositionInfo()
  const { handleChangeCurrentPosition } = usePositionAction()
  const { account, chainId } = useWeb3React()
  const { toastSuccess, toastError, toastWarning } = useToast()
  // change manager
  const onChange = useCallback(
    (val) => {
      handleChangeCurrentPosition(val)
    },
    [handleChangeCurrentPosition],
  )
  const { handleApprove } = useApprove(account, currentPosition?.mintTokenInfo.settlementCurrency.address)
  const { onCheckApprove } = useCheckApprove()
  const checkApprove = useCallback(async () => {
    if (currentPosition && currentPosition.isEnable) {
      const txInfo = await onCheckApprove(
        account,
        currentPosition.mintTokenInfo.settlementCurrency.address,
        currentPosition.positionAddress,
      )
      if (txInfo && txInfo._hex > 0) {
        setApproveStatus(true)
      } else {
        setApproveStatus(false)
      }
      return txInfo
    }
    return 0
  }, [account, currentPosition, onCheckApprove])

  const onApprove = useCallback(
    async (callback?) => {
      if (currentPosition && currentPosition.isEnable) {
        try {
          if (callback) {
            callback(true)
          }
          const info = await handleApprove(currentPosition.positionAddress)
          if (info.hash) {
            const result = await info.wait()
            if (result) {
              checkApprove().then((approveTransactionInfo) => {
                if (callback) {
                  callback(false)
                }
                if (approveTransactionInfo._hex > 0) {
                  setApproveStatus(true)
                  toastSuccess(
                    ToastTitle.success,
                    <TransactionSuccessInfo txHash={result.transactionHash} content="The approve has been successfully" />,
                  )
                } else {
                  setApproveStatus(false)
                  toastWarning('Approve Warning', 'Approval is zero. You will not be able to perform any transactions.')
                }
              })
            }
          }
        } catch (err: any) {
          if (callback) {
            callback(false)
          }
          toastError(ToastTitle.failed, `${err.reason}`)
        }
      }
    },
    [currentPosition, handleApprove, checkApprove, toastSuccess, toastWarning, toastError],
  )

  const collateralRatio = useMemo(() => {
    if (currentPosition && currentPosition.collateralRatio) {
      if (currentPosition.collateralRatio === Number.POSITIVE_INFINITY) {
        return `+${Infinite}`
      }
      if (currentPosition.collateralRatio === Number.NEGATIVE_INFINITY) {
        return `-${Infinite}`
      }
      return TokenUtils.formatTokenAmount(currentPosition.collateralRatio, 2)
    }
    return '0'
  }, [currentPosition])
  const prepaidInterestCoverage = useMemo(() => {
    if (currentPosition && currentPosition.prepaidInterestCoverage) {
      if (currentPosition.prepaidInterestCoverage === Number.POSITIVE_INFINITY) {
        return '+'
      }
      return TokenUtils.formatTokenAmount(currentPosition.prepaidInterestCoverage, 0)
    }
    return '0'
  }, [currentPosition])
  const prepaidInterest = useMemo(() => {
    if (currentPosition) {
      const pi = toLargeUnits(
        currentPosition.positionStatus.interestGuaranteed
          .sub(currentPosition.positionStatus.obligation)
          .sub(currentPosition.positionStatus.epochDebt)
          .sub(currentPosition.positionStatus.estimatedObligationToday),
        currentPosition.mintTokenInfo.settlementCurrency.decimals,
      )
      return TokenUtils.formatTokenAmount(Number(pi), 4)
    }
    return 0
  }, [currentPosition])

  useEffect(() => {
    checkApprove()
    if (expanded && Number(expanded) !== -1) {
      if (oBox && expanded !== 'panel1' && expanded !== 'panel2') {
        setTimeout(() => {
          const panel = document.querySelector(`#${expanded}`)

          if (panel) {
            panel.scrollIntoView()
          }
        }, 500)
      }
    }
  }, [checkApprove, currentPosition, expanded])

  const allowedAssets = useMemo(() => {
    if (!currentPositionTokenInfo) {
      return null
    }
    const currentTokenPrice = currentPositionTokenInfo.tokenPrice
    const currentAllowedTokens = currentPositionTokenInfo.allowedTokens
    const currentAllowedTokenBalance = currentPositionTokenInfo.allowedTokens?.balances
    const usdt = currentPositionTokenInfo.WBTCToUSD
    return Object.values(currentAllowedTokens.tokens).map((tokenInfo) => (
      <div className="item currency" key={tokenInfo.symbol}>
        <div className="icon_symbol">
          <Logo
            token={tokenInfo.logo}
            symbol={tokenInfo.symbol}
            imgprops={{
              style: {
                marginRight: 10,
              },
            }}
          />
          {tokenInfo.symbol}
        </div>
        <FlexWrap className="balance" style={{ flexDirection: 'column', alignItems: 'flex-end' }}>
          <span>
            {(currentAllowedTokenBalance &&
              toLargeUnits(currentAllowedTokenBalance[tokenInfo.symbol], tokenInfo.decimals)) ||
              0}
          </span>
          <span>
             $
            {(currentAllowedTokenBalance &&
              TokenUtils.formatCurrencyTwoDecimals(
                (Number(currentAllowedTokenBalance[tokenInfo.symbol]) *
                  Number(currentTokenPrice[tokenInfo.symbol]?.price) *
                  usdt) /
                  10 ** currentPosition.mintTokenInfo.settlementCurrency.decimals,
              )) ||
              0}
          </span>
        </FlexWrap>
      </div>
    ))
  }, [currentPosition, currentPositionTokenInfo])

  const mintTokens = useMemo(() => {
    if (isSupportedChainId(chainId)) {
      const _tokens = mintTokenChainConfigs[chainId as SupportedChainId].tokens
      return Object.keys(mintTokenChainConfigs[chainId as SupportedChainId].tokens).map((key) => {
        return {
          token: _tokens[key as MintTokenKey].symbol,
          address: _tokens[key as MintTokenKey].address,
          decimals: _tokens[key as MintTokenKey].decimals,
          icon: () => (
            <Logo
              imgprops={{ style: { marginRight: 10 } }}
              token={_tokens[key as MintTokenKey].logo}
              symbol={_tokens[key as MintTokenKey].symbol}
            />
          ),
        }
      })
    }
    return []
  }, [chainId])

  return (
    <>
      <Title style={{ marginBottom: 4 }}>Collateral Management</Title>
      <SubTitle>
        Make sure the collateral ratio is higher than the Liquidation Ratio, and the Prepaid Interest Coverage is higher
        than the Liquidation Threshold.
      </SubTitle>
      <Select isGroup={false} options={mintTokens} selectValue={currentMintToken} onChange={onChange} />
      <Box>
        {!currentPosition?.isEnable ? (
          <Link to="/collateral" style={{ color: '#2AA1FF' }}>
            Please enable the collateral first
            <ArrowLine />{' '}
          </Link>
        ) : (
          <Grid key={currentMintToken} container spacing={2.5}>
            <Grid item xs={8}>
              <ScrollWrapper ref={oBox}>
                <TipWrap>
                  <TipPanel>
                    <div title={`Collateral Ratio: ${Number(collateralRatio) > 0 ? `${collateralRatio}%`: collateralRatio}`}>
                      <h2>Collateral Ratio</h2>
                      {!currentPosition.collateralRatio ? (
                        <p className="tip" style={{ lineHeight: '85px' }}>
                          Add collateral before mint
                        </p>
                      ) : (
                        <p className="num">
                          {currentPosition.collateralRatio ? (
                            <>
                              <span
                                style={{
                                  color: getValueColor(currentPosition?.collateralRatio),
                                }}
                              >
                                {collateralRatio}
                              </span>
                              {currentPosition?.collateralRatio === Number.POSITIVE_INFINITY ? null : (
                                <sub
                                  style={{
                                    color: getValueColor(currentPosition?.collateralRatio),
                                  }}
                                >
                                  %
                                </sub>
                              )}
                            </>
                          ) : (
                            <span
                              style={{
                                color: 'rgb(255, 99, 8)',
                              }}
                            >
                              0
                            </span>
                          )}
                        </p>
                      )}
                      <p className="tip">Liquidation Ratio{LiquidationThresholdValue.ratio}%</p>
                    </div>
                    <Slide
                      style={{ height: '100%' }}
                      value={
                        currentPosition.isEnable && collateralRatio !== '0'
                          ? (currentPosition?.collateralRatio - 100) / 5
                          : 0
                      }
                    />
                  </TipPanel>
                  <TipPanel>
                    <div title={`Prepaid Interest Coverage: ${prepaidInterestCoverage} days`}>
                      <h2>Prepaid Interest Coverage</h2>
                      {!currentPosition.prepaidInterestCoverage ? (
                        <p className="tip" style={{ lineHeight: '85px' }}>
                          Add collateral before mint
                        </p>
                      ) : (
                        <p className="num guarantee">
                          {currentPosition.prepaidInterestCoverage ? (
                            <>
                              <sub
                                style={{
                                  color: getValueColor(currentPosition.prepaidInterestCoverage),
                                }}
                              >
                                
                              </sub>
                              <span
                                style={{
                                  color: getValueColor(currentPosition.prepaidInterestCoverage),
                                }}
                              >
                                {prepaidInterestCoverage}
                              </span>
                              {currentPosition.prepaidInterestCoverage === Number.POSITIVE_INFINITY ? null : (
                                <sub
                                  style={{
                                    color: getValueColor(currentPosition.prepaidInterestCoverage),
                                  }}
                                >
                                  days
                                </sub>
                              )}
                            </>
                          ) : (
                            <span
                              style={{
                                color: 'rgb(255, 99, 8)',
                              }}
                            >
                              0
                            </span>
                          )}
                        </p>
                      )}
                      <p className="tip">Liquidation Threshold: {LiquidationThresholdValue.days} days</p>
                      {/*   <p className='tip'>Min. Mint Guarantee days: {Constants.minMintGDays} days</p>
                       */}{' '}
                    </div>
                    <Slide style={{ height: '100%' }} value={currentPosition.prepaidInterestCoverage / 5} />
                  </TipPanel>
                </TipWrap>
                {/* collateral info */}
                <BeforeMintAction onApprove={onApprove} approveStatus={approveStatus} />
                <ImproveGuaranteeDays onApprove={onApprove} approveStatus={approveStatus} />
                <ImproveCollateralRatio onApprove={onApprove} approveStatus={approveStatus} />
                <TradingThroughAvatar onApprove={onApprove} approveStatus={approveStatus} />
                <WithdrawCollateral onApprove={onApprove} approveStatus={approveStatus} />
                <GeneralTransfer onApprove={onApprove} approveStatus={approveStatus} />
                {/*
                <ImproveCollateralRatio/>
                <WithdrawCollateral managerInfo={managerData} guaranteeBalance={guaranteeBalance}/>
                */}
              </ScrollWrapper>
            </Grid>
            <Grid item xs={4} className="account_panel">
              <CollateralInfoWrap>
                <h3 className="m_title">Total Collateral</h3>
                <div className="list guarantee">
                  <div className="item">
                    <h4 className="item_title">
                      <img src={Assets.PrepaidInterest} alt="" />
                      Prepaid Interest
                    </h4>
                  </div>
                  <div className="item currency">
                    <span className="icon_symbol">
                      <Logo
                        token={currentPosition?.mintTokenInfo.settlementCurrency.logo}
                        symbol={currentPosition?.mintTokenInfo.settlementCurrency.symbol}
                      />
                      <span
                        style={{
                          marginLeft: '10px',
                        }}
                      >
                        {currentPosition?.mintTokenInfo.settlementCurrency.symbol}
                      </span>
                    </span>
                    <span>{prepaidInterest}</span>
                  </div>
                </div>
                <div className="list avatar" style={{ marginBottom: 12 }}>
                  <div className="item">
                    <h4 className="item_title">
                      <img src={Assets.Collateral} alt="" />
                      Collateral
                    </h4>
                  </div>
                  <div className="item item_head">
                    <span>Ticker</span>
                    <span>Holdings - In Collateral</span>
                  </div>
                  {allowedAssets}
                </div>
                <div className="avatar_address">
                  <span>Collateral Account Address: &nbsp;&nbsp;</span>{' '}
                  <CopyToClipboard toCopy={currentPosition?.positionAddress || ''}>
                    {currentPosition
                      ? `${currentPosition?.positionAddress.slice(0, 6)}...${currentPosition?.positionAddress.slice(
                          -6,
                        )}`
                      : ''}
                  </CopyToClipboard>
                </div>
              </CollateralInfoWrap>
              <CollateralInfoWrap>
                <h3 className="asset_list_title">My Outstanding Debt</h3>
                <FlexWrap className="outstandingDebt" style={{ alignItems: 'center', justifyContent: 'space-between' }}>
                  <FlexWrap style={{ alignItems: 'center' }}>
                    <Fbtc20Icon className="icon" />
                    {currentPosition?.mintTokenInfo.symbol}
                  </FlexWrap>
                  <span>
                    {TokenUtils.formatFourDecimals(
                      Number(toLargeUnits(currentPosition.balance, currentPosition.mintTokenInfo.decimals)),
                    )}
                  </span>
                </FlexWrap>
              </CollateralInfoWrap>
              <CollateralInfoWrap style={{ padding: 30 }}>
                <img src={Assets.CollateralManage} style={{ width: '100%' }} alt="" />
              </CollateralInfoWrap>
            </Grid>
          </Grid>
        )}
      </Box>
    </>
  )
}

export default withRouter(Manage)
