import {useEffect, useState} from "react";
import {getContract} from "utils/web3React";
import PositionABI from 'config/abi/Position.json';
import {useAppSelector} from "../state";

export default function usePosContract(active,signer) {
  const [contract,setContract] = useState(null);
  const currentCpt = useAppSelector(state=> state.collateralManage.currentCPT)

  useEffect(()=>{
    if (active && signer && currentCpt && Number(currentCpt.position)) {
      const _contract = getContract(signer, PositionABI, currentCpt.position);
      setContract(_contract)
    }
  }, [active, currentCpt, signer])

  return contract
}
