import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Typography, Input } from '@mui/material/'
import Wallet from 'components/Menu/icons/Wallet'
import { Wrapper, Title, Accordion, AccordionSummary, AccordionDetails, Approve } from 'components/CommonComponent'
import usePanel from 'hooks/usePanel'
import { styled } from '@mui/material/styles'
import { AvatarIcon, ExchangeArrow, GuaranteeIcon } from 'components/uikit'
import { useWeb3React } from 'web3-react-core'
import Select from 'components/Select'
import DataWrapper from 'components/DataWrapper'
import useSystemParams from 'hooks/useSystemParams'
import usePosContract from 'hooks/usePosContract'
import contracts from 'config/constants/contracts'
import { useTransfer } from 'hooks/useManager'
import useToast from 'hooks/useToast'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import { useComputedIndexAction, usePositionAction, usePositionInfo } from 'hooks/news/usePoistion'
import Logo from 'components/Logo'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import { chainConfigs, commonTokens, valueToKeyMap } from 'config/constants/tokenConfig'
import { getSwapQuote } from 'config/constants/pool'
import { TransactionSubmittedInfo, TransactionSuccessInfo } from 'components/TransactionInfo'
import { ethers } from 'ethers'
import PositionABI from 'config/abi/Position.json'

const MethodsWrap = styled('div')`
  .balance_info {
    display: flex;
    align-content: center;
    padding: 0 12px;
    margin-bottom: 6px;
    .label {
      color: #000;
      font-size: 14px;
      font-family: PingFangSC-Medium, sans-serif;
    }
    span {
      font-family: PingFangSC-Medium, sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: rgba(0, 0, 0, 0.5);
      margin-left: 7px;
    }
  }
  .content {
    display: flex;
    height: 50px;
    background: rgba(255, 255, 255, 0.4);
    border: 1px solid rgba(207, 216, 226, 0.5);
    box-sizing: border-box;
    border-radius: 11px;
    padding: 0 12px;
    .select_custom {
      margin: 0;
      padding: 0;
      border: none;
      background: unset;
      width: 200px;
      flex-shrink: 0;
    }
    .select_custom:nth-of-type(2) {
      width: 130px;
    }
    .MuiInput-root,
    .MuiInput-input {
      text-align: right;
      width: 100%;
      border: none;
      &:before,
      &:after {
        display: none;
      }
    }
    .MuiInput-input {
      padding: 0 8px;
      border-left: 1px solid rgba(207, 216, 226, 0.5) !important;

      &::-webkit-inner-spin-button {
        display: none;
      }
    }
  }
  .func {
    font-size: 12px;
    line-height: 17px;
    color: #6ed6f3;
    text-align: right;
    padding: 0 20px;
    margin-top: 3px;
    cursor: pointer;
    user-select: none;
  }
`

enum AccountType {
  'MyWallet' = 'My Wallet',
  'Standard' = 'Collateral',
  'Settlement' = 'Prepaid Interest',
}

const accountCategoryList = [
  {
    type: AccountType.MyWallet,
    token: 'My Wallet',
    id: 'd04155c3-eb41-4c44-b28d-67bb2d515904',
    icon: Wallet,
  },
  {
    type: AccountType.Settlement,
    token: 'Prepaid Interest',
    icon: GuaranteeIcon,
    id: 'edebd20e-18a1-4bc8-a869-9b44537d810c',
  },
  {
    type: AccountType.Standard,
    token: 'Collateral',
    icon: AvatarIcon,
    id: '763c1484-3fd3-4044-b8b5-3c261947f414',
  },
]

const { ActionUser } = contracts

const ActionUserInterface = new ethers.utils.Interface(ActionUser.abi)
const getTokenDecimals = (symbol: string) => {
  const key = valueToKeyMap[symbol]
  return commonTokens[key].decimals
}

export default function GeneralTransfer(props) {
  const { approveStatus, onApprove } = props
  const [gDisStatus, setGDisStatus] = useState(true)
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const { signer, tokenPools } = useSystemParams()
  const { walletTokenBalance, setRefresh } = useHashPowerToken()
  const [trading, setTrading] = useState(false)
  const { toastInfo, toastSuccess, toastError } = useToast()

  const { currentPosition, currentPositionTokenInfo, collateralIndex } = usePositionInfo()
  const {
    onUpdateCollateralValue,
    onUpdatePrepaidInterest,
    newDisplayCollateralRatio,
    newDisplayPrepaidInterestCoverage,
    newCollateralRatio,
    newPrepaidInterestCoverage,
  } = useComputedIndexAction()
  const { handleUpdatePosition } = usePositionAction()

  const [actionData, setActionData] = useState([
    {
      accountType: AccountType.MyWallet,
      tokenType: currentPosition.mintTokenInfo.symbol,
      transferVal: 0,
      id: '1fee1a14-b8b3-4b73-b2be-1817a6d228ec',
      tokenSelectDisabled: false,
      list: [...accountCategoryList],
      token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
      decimals: currentPosition.mintTokenInfo.settlementCurrency.decimals,
    },
    {
      accountType: AccountType.Standard,
      tokenType: currentPosition.mintTokenInfo.symbol,
      transferVal: 0,
      id: '25897dcf-e843-4630-a5e1-309dc10cb1e5',
      tokenSelectDisabled: true,
      list: [...accountCategoryList],
      token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
      decimals: currentPosition.mintTokenInfo.settlementCurrency.decimals,
    },
  ])

  const avatarAssetList = useMemo(() => {
    if (currentPositionTokenInfo.allowedTokens) {
      const { balances, tokens } = currentPositionTokenInfo.allowedTokens
      console.log('tokens', tokens)
      return Object.keys(balances).reduce((acc, key) => {
        console.log('key', key)
        return {
          ...acc,
          [key]: tokens[key] ? (toLargeUnits(balances[key], tokens[key].decimals, true) as number) : 0,
        }
      }, {})
    }
    return Object.keys(valueToKeyMap).reduce((acc, key) => {
      return {
        ...acc,
        [key]: 0,
      }
    }, {})
  }, [currentPositionTokenInfo.allowedTokens])

  const tokenList = useMemo(() => {
    if (currentPositionTokenInfo.allowedTokens) {
      const { tokens } = currentPositionTokenInfo.allowedTokens
      return Object.keys(tokens).map((key) => {
        return {
          token: key,
          decimals: tokens[key].decimals,
          address: tokens[key].address,
          icon: () => <Logo imgprops={{ style: { marginRight: 10 } }} token={tokens[key].logo} symbol={key} />,
        }
      })
    }
    return []
  }, [currentPositionTokenInfo.allowedTokens])

  const transfer = useTransfer()
  const { expanded, handleChange } = usePanel()
  const { chainId, account } = useWeb3React()

  const onChangeVal = useCallback(({ target }, record) => {
    const { value } = target
    setActionData((state) => {
      const data = state.map((item) => ({ ...item, transferVal: value }))
      return data
    })
  }, [])

  const onSubmit = useCallback(async () => {
    setTrading(true)

    let data = null
    let isTransfer = false
    const transferTransactions = []
    const [transferData, recviceTarget] = actionData
    const { tokenType: tokenSymbol, accountType, transferVal } = transferData
    const transferDecimals = getTokenDecimals(tokenSymbol)
    const transferAmount = toSmallUnits(TokenUtils.formatTokenAmount(transferVal, transferDecimals), transferDecimals)
    const tokenAddress = chainConfigs[chainId].tokens[valueToKeyMap[tokenSymbol]].address
    if (transferAmount.isZero()) {
      return
    }

    const handleResult = async (txInfo) => {
      const result = await txInfo.wait()
      if (result) {
        toastSuccess(
          ToastTitle.success,
          <TransactionSuccessInfo content="The transaction has been successfully." txHash={result.hash} />,
        )
        setTrading(false)
        setGDisStatus(true)
        onChangeVal({ target: { value: 0 } }, null)
        await handleUpdatePosition()
      }
    }

    const handleError = ({ message, reason }) => {
      toastError('Failed', `${reason || message}`)
      setTrading(false)
    }

    const handleAllSettled = (results) => {
      console.log('results :>> ', results)
      setTrading(false)
      results.forEach((result) => {
        if (result.status === 'rejected') {
          toastError(ToastTitle.failed, result.reason?.reason || result.reason?.message)
        } else {
          toastSuccess(
            ToastTitle.success,
            <TransactionSuccessInfo
              content="The transaction has been successfully."
              txHash={result.value.transactionHash}
            />,
          )
          setRefresh((state) => !state)
        }
      })
    }

    const submitTransaction = async (_data: string) => {
      try {
        const positionContract = new ethers.Contract(currentPosition.positionAddress, PositionABI, signer)
        const txInfo = await positionContract.delegateCall(ActionUser.address[chainId], _data)
        if (txInfo) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
          )
          await handleResult(txInfo)
        }
      } catch (error) {
        handleError(error as any)
      }
    }

    if (accountType === AccountType.MyWallet) {
      if (recviceTarget.accountType === AccountType.Settlement) {
        data = ActionUserInterface.encodeFunctionData('addGuarantee', [transferAmount, false])
      } else if (recviceTarget.accountType === AccountType.Standard) {
        transferTransactions.push(transfer(account, tokenAddress, currentPosition.positionAddress, transferAmount))
        isTransfer = true
      }
    } else if (accountType === AccountType.Standard) {
      if (recviceTarget.accountType === AccountType.MyWallet) {
        data = ActionUserInterface.encodeFunctionData('withdrawAsset', [tokenAddress, account, transferAmount])
      } else if (recviceTarget.accountType === AccountType.Settlement) {
        data = ActionUserInterface.encodeFunctionData('addGuarantee', [transferAmount, true])
      }
    } else if (accountType === AccountType.Settlement) {
      if (recviceTarget.accountType === AccountType.MyWallet) {
        data = ActionUserInterface.encodeFunctionData('moveGuarantee', [account, transferAmount])
      } else if (recviceTarget.accountType === AccountType.Standard) {
        data = ActionUserInterface.encodeFunctionData('moveGuarantee', [
          currentPosition.positionAddress,
          transferAmount,
        ])
      }
    }

    if (!isTransfer && data) {
      await submitTransaction(data)
    } else {
      Promise.allSettled(transferTransactions)
        .then(handleAllSettled)
        .catch((e) => {
          console.log('e :>> ', e)
          toastError(ToastTitle.failed, e.reason)
          setTrading(false)
        })
    }
  }, [
    actionData,
    chainId,
    toastSuccess,
    onChangeVal,
    handleUpdatePosition,
    toastError,
    setRefresh,
    currentPosition.positionAddress,
    signer,
    toastInfo,
    transfer,
    account,
  ])

  useEffect(() => {
    const [send, recive] = actionData
    switch (send.accountType) {
      case AccountType.MyWallet:
        if (recive.accountType === AccountType.Settlement) {
          onUpdatePrepaidInterest(send.transferVal)
        } else {
          onUpdateCollateralValue({
            symbol: send.tokenType as any,
            value: Number(send.transferVal),
          })
        }
        break
      case AccountType.Standard:
        if (recive.accountType === AccountType.MyWallet) {
          const value = Number(send.transferVal)
          onUpdateCollateralValue({
            symbol: send.tokenType as any,
            value: value ? Number(`-${value}`) : value,
          })
        } else {
          const value = Number(send.transferVal)
          onUpdateCollateralValue({
            symbol: send.tokenType as any,
            value: value ? Number(`-${value}`) : value,
          })
          onUpdatePrepaidInterest(send.transferVal)
        }
        break
      case AccountType.Settlement:
        if (recive.accountType === AccountType.MyWallet) {
          const value = Number(send.transferVal)
          onUpdatePrepaidInterest(value ? Number(`-${value}`) : value)
        } else {
          const value = Number(send.transferVal)
          onUpdatePrepaidInterest(value ? Number(`-${value}`) : value)
          onUpdateCollateralValue({
            symbol: send.tokenType as any,
            value: Number(send.transferVal),
          })
        }
        break
      default:
        console.log(1)
    }
  }, [actionData, onUpdateCollateralValue, onUpdatePrepaidInterest])

  const onChangeAccount = useCallback(
    (val, record, i) => {
      setActionData((state) => {
        const data = state.map((item) => ({
          ...item,
          accountType: item.id === record.id ? val : item.accountType,
          tokenType:
            val === AccountType.Settlement ? currentPosition.mintTokenInfo.settlementCurrency.symbol : item.tokenType,
          transferVal: Number(),
        }))
        if (i === 0) {
          data[1].list = accountCategoryList.filter((it) => it.type !== data[0].accountType)
          data[0].tokenSelectDisabled = data[0].accountType === AccountType.Settlement
        }
        return data
      })
    },
    [currentPosition.mintTokenInfo.settlementCurrency.symbol],
  )

  const onChangeToken = useCallback(
    (val, __index) => {
      setActionData((state) => {
        const nextState = state.map((it) => ({ ...it, tokenType: val, transferVal: Number() }))

        if (val !== currentPosition.mintTokenInfo.settlementCurrency.symbol) {
          nextState[1].list = nextState[1].list.filter((it) => it.type !== AccountType.Settlement)
        } else {
          const settlement = accountCategoryList.find((it) => it.type === AccountType.Settlement)
          const _index = nextState[1].list.findIndex((it) => it.type === AccountType.Settlement)
          if (!(_index > -1)) {
            nextState[1].list.push(settlement)
          }
        }
        return nextState
      })
    },
    [currentPosition.mintTokenInfo.settlementCurrency.symbol],
  )

  const onMax = useCallback(
    (record) => {
      setActionData((state) => {
        const data = [...state]
        /* 
          1. wallet send to collateral //// add collateraltransfer   Total assets increase, prepaid interest remains unchanged, margin ratio increases, and the number of days covered by prepaid interest remains the same.
          2. wallet send to prepaid interest       // addGuarantee    Total assets increase, prepaid interest increases, margin ratio increases, and the number of days covered by prepaid interest remains the same.
          3. collateral send to wallet //           withdrawAsset     Total assets decrease, prepaid interest remains unchanged, margin ratio decreases, and the number of days covered by prepaid interest remains the same.
          4. collateral send to prepaid interest // addGuarantee |      Total assets remain unchanged, prepaid interest increases, margin ratio remains unchanged, and the number of days covered by prepaid interest increases.
          5. prepaid interest send to wallet /// moveGuarantee |        Total assets decrease, prepaid interest decreases, margin ratio decreases, and the number of days covered by prepaid interest decreases.
          6. prepaid interest send to collateral /// add collateral ||  Total assets remain unchanged, prepaid interest decreases, margin ratio remains unchanged, and the number of days covered by prepaid interest decreases.
          */
        const { settlementCurrency } = currentPosition.mintTokenInfo
        const { shortBalanceValue, estimatedDailyDebt, collateralValue } = currentPosition.positionStatus
        const tokenTypeDecimals = getTokenDecimals(record.tokenType)
        if (record.accountType === AccountType.MyWallet) {
          const balance = walletTokenBalance[record.tokenType] || 0
          data[0].transferVal = balance
          if (data[1].accountType === AccountType.Standard) {
            data[1].transferVal = balance + avatarAssetList[record.tokenType]
          }
          if (data[1].accountType === AccountType.Settlement) {
            data[1].transferVal = balance + collateralIndex.prepaidInterest
          }
          // data[1].transferVal = balance
        } else if (record.accountType === AccountType.Standard) {
          const collateralAssets = avatarAssetList[record.tokenType] || 0
          const maxActionTotalAssets = Number(
            TokenUtils.formatTokenAmount(1.2 * Number(toLargeUnits(shortBalanceValue, settlementCurrency.decimals)), 0),
          )
          if (collateralAssets > 0) {
            if (maxActionTotalAssets === 0) {
              data[0].transferVal = collateralAssets
              if (data[1].accountType === AccountType.MyWallet) {
                data[1].transferVal = collateralAssets + walletTokenBalance[record.tokenType]
              }
              if (data[1].accountType === AccountType.Settlement) {
                data[1].transferVal = collateralAssets + collateralIndex.prepaidInterest
              }
              return data
            }
            const maxCollateralTransfer = maxActionTotalAssets - collateralIndex.prepaidInterest
            if (data[1].accountType === AccountType.Settlement) {
              // If the calculated maximum value exceeds the asset value, use the asset value instead.
              if (maxCollateralTransfer > collateralAssets) {
                data[0].transferVal = collateralAssets
                // data[1].transferVal = collateralAssets
              } else {
                data[0].transferVal = maxCollateralTransfer
                // data[1].transferVal = maxCollateralTransfer
              }
            }
            if (data[1].accountType === AccountType.MyWallet) {
              const amountOutPrice = getSwapQuote(
                chainId,
                record.tokenType,
                settlementCurrency.symbol,
                toSmallUnits(collateralAssets, tokenTypeDecimals),
                tokenPools,
              )
              const otherTokenWbtc = collateralValue.sub(amountOutPrice)
              const actionCurrentMaxValue =
                maxCollateralTransfer - Number(toLargeUnits(otherTokenWbtc, settlementCurrency.decimals))

              const amountInCurrentMaxValue = getSwapQuote(
                chainId,
                settlementCurrency.symbol,
                record.tokenType,
                toSmallUnits(
                  TokenUtils.formatTokenAmount(actionCurrentMaxValue, settlementCurrency.decimals),
                  settlementCurrency.decimals,
                ),
                tokenPools,
              )
              // Calculate the maximum operable value for the current cryptocurrency.
              const maxTransterCurrentToken = toLargeUnits(amountInCurrentMaxValue, tokenTypeDecimals, true) as number
              // If the calculated maximum value exceeds the asset value, use the asset value.
              if (maxTransterCurrentToken > collateralAssets) {
                data[0].transferVal = collateralAssets
                // data[1].transferVal = collateralAssets
              } else if (maxTransterCurrentToken > 0) {
                data[0].transferVal = Math.floor(collateralAssets - maxTransterCurrentToken)
                // data[1].transferVal = Math.floor(collateralAssets - maxTransterCurrentToken)
              } else {
                data[0].transferVal = collateralAssets
                // data[1].transferVal = collateralAssets
              }
            }
            if (data[1].accountType === AccountType.MyWallet) {
              data[1].transferVal = data[0].transferVal + walletTokenBalance[record.tokenType]
            }
            if (data[1].accountType === AccountType.Settlement) {
              data[1].transferVal = data[0].transferVal + collateralIndex.prepaidInterest
            }
          }
        } else if (record.accountType === AccountType.Settlement) {
          if (data[1].accountType === AccountType.MyWallet) {
            const maxPrepaidInterest =
              20 * Number(toLargeUnits(estimatedDailyDebt.toNumber(), settlementCurrency.decimals))
            const maxActionPrepaidInterest = collateralIndex.prepaidInterest - maxPrepaidInterest

            if (maxActionPrepaidInterest > 0) {
              data[0].transferVal = Number(
                TokenUtils.formatTokenAmount(maxActionPrepaidInterest, settlementCurrency.decimals),
              )
            }
          }
          if (data[1].accountType === AccountType.Standard) {
            const maxPrepaidInterest =
              20 * Number(toLargeUnits(estimatedDailyDebt.toNumber(), settlementCurrency.decimals))
            const maxActionPrepaidInterest = collateralIndex.prepaidInterest - maxPrepaidInterest
            if (maxActionPrepaidInterest > 0) {
              data[0].transferVal = maxActionPrepaidInterest
            }
          }
          if (data[1].accountType === AccountType.MyWallet) {
            data[1].transferVal = data[0].transferVal + walletTokenBalance[record.tokenType]
          }
          if (data[1].accountType === AccountType.Standard) {
            data[1].transferVal = data[0].transferVal + avatarAssetList[record.tokenType]
          }
        }
        data[1].transferVal = Number(TokenUtils.formatFourDecimals(data[1].transferVal))
        return data
      })
    },
    [
      avatarAssetList,
      chainId,
      collateralIndex,
      currentPosition.mintTokenInfo,
      currentPosition.positionStatus,
      tokenPools,
      walletTokenBalance,
    ],
  )

  useEffect(() => {
    setActionData((state) => {
      return [
        state[0],
        {
          ...state[1],
          list: accountCategoryList.filter((it) => it.type !== state[0].accountType),
        },
      ]
    })
  }, [])

  useEffect(() => {
    if (trading) {
      setGDisStatus(true)
      return
    }
    const [left] = actionData
    const { decimals } = left
    if (!left || !decimals) return
    let leftBalance = 0
    const { transferVal } = left
    const transferAmout = toSmallUnits(
      TokenUtils.formatTokenAmount(transferVal, commonTokens[valueToKeyMap[left.tokenType]].decimals),
      commonTokens[valueToKeyMap[left.tokenType]].decimals,
    )
    if (transferAmout.isZero()) {
      setGDisStatus(true)
      return
    }
    if (left.accountType === AccountType.MyWallet) {
      leftBalance = walletTokenBalance[left.tokenType] || 0
    }
    if (left.accountType === AccountType.Settlement || left.accountType === AccountType.Standard) {
      if (left.accountType === AccountType.Settlement) {
        // if ()
        const { interestGuaranteed } = currentPosition.positionStatus
        if (transferAmout.gt(interestGuaranteed)) {
          setGDisStatus(true)
          return
        }
      }
      if (left.accountType === AccountType.Standard) {
        const val = avatarAssetList[left.tokenType] || 0
        if (transferVal > val) {
          setGDisStatus(true)
          return
        }
      }
      if (newCollateralRatio < 120 || newPrepaidInterestCoverage < 20) {
        setGDisStatus(true)
      } else {
        setGDisStatus(false)
      }
      return
    }

    if (left.transferVal <= 0 || left.transferVal > leftBalance || trading) {
      setGDisStatus(true)
    } else {
      setGDisStatus(false)
    }
  }, [
    actionData,
    avatarAssetList,
    currentPosition.positionStatus,
    newCollateralRatio,
    newPrepaidInterestCoverage,
    trading,
    walletTokenBalance,
  ])

  return (
    <Wrapper>
      <Title>General Transfer</Title>
      <Accordion id="panel15" expanded={expanded === 'panel15'} onChange={handleChange('panel15')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Freestyle Transfer</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {actionData.map((item, index) => {
            let avatarAssetBalance = (avatarAssetList && avatarAssetList[item.tokenType]) || 0
            let walletAssetBalance = (walletTokenBalance && walletTokenBalance[item.tokenType]) || 0
            if (String(avatarAssetBalance).includes('.')) {
              avatarAssetBalance = TokenUtils.formatFourDecimals(avatarAssetBalance)
            }

            if (String(walletAssetBalance).includes('.')) {
              walletAssetBalance = TokenUtils.formatFourDecimals(walletAssetBalance)
            }

            return (
              <div key={item.id}>
                <MethodsWrap>
                  <div className="balance_info">
                    <span className="label">Balance: </span>
                    {item.accountType === AccountType.Standard && (
                      <span>
                        {avatarAssetBalance} {item.tokenType}
                      </span>
                    )}
                    {item.accountType === AccountType.MyWallet && (
                      <span>
                        {walletAssetBalance} {item.tokenType}
                      </span>
                    )}
                    {item.accountType === AccountType.Settlement && (
                      <span>
                        {TokenUtils.formatFourDecimals(collateralIndex.prepaidInterest)}{' '}
                        {currentPosition.mintTokenInfo.settlementCurrency.symbol}
                      </span>
                    )}
                  </div>
                  <div className="content">
                    <Select
                      options={item.list}
                      onChange={(val) => {
                        onChangeAccount(val, item, index)
                      }}
                      selectValue={item.accountType}
                    />
                    <Select
                      options={tokenList}
                      onChange={(val) => {
                        onChangeToken(val, index)
                      }}
                      selectValue={item.tokenType}
                      selectDisabled={index === 1 ? true : item.tokenSelectDisabled}
                    />
                    <Input
                      value={item.transferVal}
                      type="number"
                      onChange={(event) => {
                        const inputValue = event.target.value
                        let formattedValue = ''
                        const cleanedValue = inputValue.replace(/[^\d.]/g, '')
                        if (cleanedValue.includes('.')) {
                          const parts = cleanedValue.split('.')
                          parts[1] = parts[1]?.slice(0, commonTokens[valueToKeyMap[item.tokenType]].decimals)
                          formattedValue = parts.join('.')
                        } else {
                          formattedValue = cleanedValue
                        }
                        if (Number(formattedValue) > 100000000) {
                          formattedValue = '100000000'
                        }
                        onChangeVal({ target: { value: formattedValue } }, item)
                      }}
                      disabled={index === 1}
                    />
                  </div>

                  <div hidden={index === 1} className="func">
                    <span
                      onClick={() => {
                        onMax(item)
                      }}
                      onKeyUp={() => null}
                      tabIndex={0}
                      role="button"
                    >
                      Max
                    </span>
                  </div>
                </MethodsWrap>

                {index === 0 ? (
                  <div style={{ display: 'flex', alignItems: 'center', height: 30, justifyContent: 'center' }}>
                    <ExchangeArrow
                      onClick={() => {
                        setActionData((state) => {
                          const list1 = state[0]
                          const list2 = state[1]
                          const data = state.reverse()
                          return data.map((it, i) => ({
                            ...it,
                            list:
                              i === 0
                                ? list1.list
                                : accountCategoryList.filter((ele) => ele.type !== list2.accountType),
                            tokenSelectDisabled: i === 0 ? list1.tokenSelectDisabled : list2.tokenSelectDisabled,
                          }))
                        })
                      }}
                      style={{ fill: 'none', transform: 'rotate(90deg)', cursor: 'pointer', width: 22 }}
                    />
                  </div>
                ) : null}
              </div>
            )
          })}
          <DataWrapper
            data={[
              [DataIndexName.CollateralRatio, collateralIndex.displayRatio, newDisplayCollateralRatio],
              [
                DataIndexName.PrepaidInterestCoverage,
                collateralIndex.displayCoverage,
                newDisplayPrepaidInterestCoverage,
              ],
            ]}
          />
          {approveStatus ? (
            <Approve style={{ boxShadow: 'none' }} disabled={gDisStatus} onClick={onSubmit}>
              Submit
            </Approve>
          ) : (
            <Approve
              style={{ boxShadow: 'none' }}
              disabled={approveBtnStatus}
              onClick={() => {
                onApprove((state) => {
                  setApproveBtnStatus(state)
                })
              }}
            >
              Approve
            </Approve>
          )}
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
